<template>
  <div>
    <h1>{{ message }}fdsffdsafds</h1>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      message: ''
    };
  },
  mounted() {
    axios.get(`${process.env.VUE_APP_API_URL}/api/hello`)
      .then(response => {
        this.message = response.data;
      })
      .catch(error => {
        console.error(error);
      });
  }
}
</script>
